.loginPageContainer {
  grid-template-columns: 30% 70%;
  display: grid;
  width: 100%;
  /* column-gap: 200px; */
}
.loginBox {
  padding: 30px;
  display: grid;
  grid-template-rows: 30% 10% repeat(3, 1fr);
  /* justify-items: center; */
}
.loginHead {
  font-size: 100px;
  padding-bottom: 40px;
}
.flexIt {
  display: flex;
  justify-content: space-between;
  padding: 40px;
}
.imageLogin {
  width: 100%;
  height: 100vh;
  /* height: 400p; */
}
.buttonLogin {
  width: 100%;
  height: 40px;
  padding: 10px;
  border-radius: 80px;
}
